  align-items: center;
  justify-content: space-between;
  padding-left: 1vw;
  padding-right: 1vw;
  padding-top: 3vh;
  color: white;
}

.logo {
  /* height: 40vmin; */
  height: 4vh;
  margin-top: -3vh;
  object-fit: fill;
}


.Body {
  padding: 2vh;
  font-size: 17px;
  text-align: left;
  color: white;
  background-color: #1f1f1f;
  text-decoration: none;
  
}

.Body_No_Page {
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 2vh;
  min-height: 86.1vh;
  font-size: 17px;
  text-align: left;
  color: white;
  background-color: #000000;
