.Personal_Photo{
  object-fit: fill;
  object-position: left;
  justify-content: left;
  align-items: left;
  width: 25vw;
  border-radius: 25px;
}

.container {
  display: flex;
  justify-content: space-between; /* Adjust as needed */
}

.item {
  padding: 10px;
  /* border: 1px solid #000; */
  text-align: left;
}
h4{
  font-weight: normal;
}
h5{
  text-indent: 1em;
  font-weight: normal;  
}
h6{
padding-left: 2em;
font-weight: normal;
}
.link{
  font-size: 17px;
  text-align: left;
  color: white;
}

.form {
  max-width: 400px;
  margin: 0 auto;
  border: red;
  border-width: 10px;
}

.div {
  margin-bottom: 15px;
}

.label {
  display: block;
  margin-bottom: 5px;
}

.input, .textarea {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}

.button {
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.button:hover {
  background-color: #0056b3;
}


/* NavBar.css */
.topnav {
  margin-top: -3vh;
  overflow: hidden;
  background-color: #1f1f1f;
  border-radius: 25px;
}

.topnav a {

  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
/*  rgb(62, 112, 242)*/
.topnav a:hover {
  background-color: white;
  color: black;
}

.topnav a.active {
  background-color: rgb(62, 112, 242);
  color: white;
}

.topnav .icon {
  display: none;
}

@media screen and (max-width: 900px) {
  .topnav a:nth-child(n+0) {
    display: none;
  }
  .topnav a.icon {
    float: right;
    display: block;
  }
}

@media screen and (max-width: 900px) {  
   .topnav.responsive {
    position: relative;
    float: right;
    margin-top: auto;
    overflow: hidden;
  } 
  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top:00;
    
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }   
}  


.footer {
  text-align: center;
  padding: 20px;
  color: white;
  background-color: rgb(0, 0, 53);
}

.footer p {
